<template>
<div>
  <div class="container" v-if="isShow">
    <h2>编辑博客</h2>
    <h4>博客标题</h4>
    <input type="text" v-model="blogs.title">
    <h4>博客内容</h4>
    <textarea v-model="blogs.content" name="" id="" cols="30" rows="10" ></textarea>
    <div class="taginput">
      <label for="vue"><input v-model="blogs.taglist" id="vue" type="checkbox" value="vue" >vue</label>
      <label for="react"><input v-model="blogs.taglist" id="react" type="checkbox" value="react" >react</label>
      <label for="angular"><input v-model="blogs.taglist" id="angular" type="checkbox" value="angular">angular</label>
      <label for="text1"><input v-model="blogs.taglist" type="checkbox" id="text1" value="前端" >前端</label>
      <label for="text2"><input v-model="blogs.taglist" type="checkbox" id="text2" value="日志" >日志</label>
      <label for="text3"><input v-model="blogs.taglist" type="checkbox" id="text3" value="android" >android</label>
    </div>
    <input type="date" v-model="blogs.date" >
    <div class="button" @click="update">
      编辑博客
    </div>
  </div>
  <div v-else class="bottom">
    <h3>博客编辑成功</h3>
    <p>1s后返回主页</p>
  </div>
</div>
</template>
<script>
export default {
  data:function(){
    return{
      blogs:{},
      isShow:true,
      id:this.$route.params.id
    }
  },
  created(){
    //获取数据
    let query= Bmob.Query('blogs');
    query.get(this.id).then(res =>{
      // console.log('请求成功');
      console.log(res);
      this.blogs=res;
    }).catch(err =>{
      console.log('请求失败');
      console.log(err);
    })
    console.log('编辑博客');
  },
  methods:{
    //更新数据
    update:function(){
      this.isShow=false;
      let query = Bmob.Query('blogs');
      query.get(this.id).then(res =>{
        res.set('title',this.blogs.title);
        res.set('content',this.blogs.content);
        res.set('date',this.blogs.date);
        res.set('taglist',this.blogs.taglist);
        res.save();
        setTimeout(() => {
          this.$router.push('/');
        }, 1000);
      }).catch(err =>{
        console.log(err);
        console.log('更新失败');
      })
      // query.set('title',this.blogs.title);
      // query.set('content',this.blogs.content);
      // query.set('date',this.blogs.date);
      // query.set('taglist',this.blogs.taglist);
      // query.save().then(res =>{
      //   console.log('更新数据');
      //   setTimeout(() => {
      //     this.$router.push('/');
      //   }, 1000);
      // }).catch(err =>{
      //   console.log(err);
      // })
    }
  }
}
</script>





<style scoped>
.container{
  width: 900px;
  margin: 0 auto;
}
.button{
  cursor: pointer;
  margin-top: 1.5em;
  text-align: center;
  width: 100px;
  padding: 10px 20px;
  background-color: #909399;
}
.button:hover{
  background-color: #409EFF;
  color: #303133;
}
input[type='text']{
  display: block;
  width:100%;
  height: 2em;
}
textarea{
  display: block;
  width: 100%;
  height: 400px;
  font-size: 16px;
}
.taginput{
  margin-top: 1em;
  margin-bottom: 1em;
}
.taginput label{
  height: 2em;
  line-height: 2em;
  margin-right: 1em;
}
.bottom{
  margin-top: 2em;
  text-align: center;
}
</style>
</style>
